@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,500;0,600;0,700;0,800;1,400;1,500&display=swap);

body{
  margin:0;
  padding:0;
  box-sizing: border-box;
  overflow-x:hidden;

 
}

html{

  font-size:62.5%;
}
 


.slider{

  margin-top:-50px;
}
  .carousel__container>img{
    width:100vw;
    height:102vh;
  
  }

  .carousel.carousel-slider .control-arrow{

    display: none;
  }

  .carousel .carousel-status {
    display: none;
  }

.carousel .thumbs-wrapper{

  display: none;
}

.carousel .control-dots {
    left:-41%;
    bottom:80px !important;
   
}

.carousel .control-dots .dot.selected{
  background-color: goldenrod !important;

}
.carousel .control-dots .dot{

  width:20px !important;
  height:20px !important;
  background-color:white !important;
  opacity:1 !important;
}

.carousel .control-dots .dot:focus{

  outline: none !important;
}

.carousel__detailes{

  position: absolute;
  top:30%;
  left:5%;
  display:flex;
  flex-direction: column;
  align-items:flex-start;
}

.carousel__detailes>h1,
.carousel__detailes>p{

  color:white;

}

.carousel__detailes>button{

  border-radius:500px;
  padding:10px;
  width:200px;
  border:none;
  font-size:25px;
  font-weight:400;
  color:white;
  background-color:goldenrod;
  cursor: pointer;
}

.carousel__detailes>button:hover{

  background-color:white;
  color:black;
}
.carousel__detailes>h1{

  font-size:35px;
  margin-bottom:40px;
}
.carousel__detailes>p{

  font-size:20px;
  margin-top:-20px;
  letter-spacing:2px;
}

.divider{

  position:absolute;
  top:15%;
   border:1.5px solid white;
   width:92%;
   border-top:0;
   border-left:0;
   border-right:0;
   margin:10px 30px;
   margin-right:20px;
}
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {

    .slider{

        margin-top:-130px;
      }
    .carousel__container>img{
      
        object-fit:cover;
       
      
      }
      .carousel .control-dots {
        left:-34%;
        bottom:150px !important;}

        .carousel .control-dots .dot{

            width:12px !important;
            height:12px !important;
           
          }

          .carousel__detailes>button{
            font-size:15px;
            }
            .carousel__detailes>h1{

                font-size:30px;
              }
              .carousel__detailes>p{

                font-size:15px;
                margin-top:-10px;
             
              }
          
              .divider{
                  display: none;
              }
}

@media only screen 
and (min-device-width : 768px) 
and (max-device-width : 1024px) {

    .carousel .control-dots {
        left:-38%;
        bottom:350px !important;}

}
.header{

    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index:1;
    position: relative;
    top:50px;
}
.header__right{

    display: flex;
}

.header__navlinks{
    display: flex;
    align-items: center;
    margin-right:8rem;
    cursor: pointer;
}

.header__navlinks>h2,
.header__navlinks>.MuiSvgIcon-root{
    color:white;
    
}

.header__navlinks>h2{
    margin-right:2px;
    font-weight:400;
    letter-spacing:1.5px;
}
.header__navlinks>.MuiSvgIcon-root{
    font-size:20px;
    
}
.header__left>img{

    width:70px;
    height:70px;
    object-fit:contain;
    margin-top:-45%;
    margin-left:65%;
}

.header__right>.MuiSvgIcon-root{
     
    display: none;
}


/* Smartphones (portrait and landscape) ----------- */
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {

    .header__navlinks{
        margin-right:1rem;
    }
    
    .header{

        display: flex;
        justify-content: space-between;
        align-items: center;
        z-index:1;
        position: relative;
        top:10px;
    }
    

   .header__right{
       display: flex;
       flex-direction: column;   
       align-items: center; 
    
    }
    .header__right>.MuiSvgIcon-root{
     
        color:white;
        font-size:35px;
        margin-right:30px;
        display: block;
    }
    .header__left>img{

        width:50px;
        height:50px;
        margin-top:-35px;
       
    }
   
    
}

